import { Injectable } from '@angular/core';
import { Roles } from './roles';

@Injectable()
export class RolUser {

     public mail: string;
     private rol_id:Roles;


     constructor(mail:string, rol_id: Roles) {
         this.mail = mail;
         this.rol_id = rol_id;
     }

 }


